@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
body {
  font-family: 'Poppins', sans-serif;
  background: #fafafa;
}

p {
  font-family: 'Poppins', sans-serif;
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.7em;
  color: rgb(100, 100, 100);
}

a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.wrapper {
  display: flex;
  width: 100%;
}

.animate-box {
  opacity: 0;
}

.creditImg {
  font-size: 75%;
  text-align: center;
}

.creditImg img, .creditImg p {
  max-width: 100%;
  word-wrap: break-word;
}

.fadeIn {
  -webkit-animation: fadeIn 1.5s;
  /* Safari 4+ */
  /* Fx 5+ */
  /* Opera 12+ */
  animation: fadeIn 1.5s;
  /* IE 10+, Fx 29+ */
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* sidebar css */

#sidebar {
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    background: #fff;
    color: #000;
    border: 1px solid rgb(200, 200, 200);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
  
  #sidebar.active {
    margin-left: -250px;
  }
  
  #sidebar .sidebar-header {
    padding: 20px;
    background: #fff;
  }
  
  #sidebar ul.components {
    padding: 10px 0;
    border-bottom: 1px solid rgb(200, 200, 200);
  }
  
  #sidebar ul p {
    color: #000;
    padding: 0px 10px;
    margin: auto;
    text-align: center;
  }
  
  #sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
  }
  
  #sidebar ul li a:hover {
    background: rgb(200, 200, 200);
    cursor: pointer;
  }
  
  a[data-toggle="collapse"] {
      position: relative;
  }
  
  .dropdown-toggle::after {
      display: block;
      position: absolute;
      top: 50%;
      right: 20px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
  }
  
  ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #fff;
  }

  /* mobile */

@media (max-width: 768px) {
  #sidebar {
      margin-left: -250px;
  }
  #sidebar.active {
      margin-left: 0;
  }
}
/* content css */

#content {
  width: calc(100% - 250px);
  padding: 40px;
  min-height: 100vh;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #fff;
}

#content.active {
  width: 100%;
}

button span {
  margin: 0 0 0 5px;
}

.navbar {
  padding: 15px 10px;
  background: #fff;
  border: none;
  border-radius: 0;
  margin-bottom: 40px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.line {
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ddd;
  margin: 40px 0;
}

/* mobile */

@media (max-width: 768px) {
  #content {
    width: 100%;
  }
  #content.active {
    width: calc(100% - 250px);
  }
  button span {
    display: none;
  }
}
.link {
    color: #000;
    font-weight: 400;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.link:hover{
    cursor: pointer;
    background: #000;
    color: #fff;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.fadeInRight {
    -webkit-animation: fadeInRight 2s;
    /* Safari 4+ */
    /* Fx 5+ */
    /* Opera 12+ */
    animation: fadeInRight 2s;
    /* IE 10+, Fx 29+ */
}

.fadeInLeft {
    -webkit-animation: fadeInLeft 2s;
    /* Safari 4+ */
    /* Fx 5+ */
    /* Opera 12+ */
    animation: fadeInLeft 2s;
    /* IE 10+, Fx 29+ */
}

#resume li {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: rgb(100, 100, 100);
}

.center-cropped {
    object-fit: cover;
    /* Do not scale the image */
    object-position: center;
    /* Center the image within the element */
    height: 100px;
    width: 100px;
    /* max-width: 100%; */
}
#project #projectNavigation div {
    padding: 5px;
}
.carousel-inner>.carousel-item {
    height: 70vh;
}

.carousel-inner>.carousel-item>.carousel-caption {
    bottom: 0;
    padding: 0;
}

.carousel-inner>.carousel-item>.carousel-caption>p {
    margin: 5px;
}

.carousel-inner>.carousel-item>img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    max-height: 700px;
    width: auto;
}

.carousel-control-prev, .carousel-control-next {
    background-image: none;
}

.carousel-indicators li {
    background-color: #000;
}

.carousel-indicators {
    bottom: unset;
    margin: 0;
}

#carouselContainer {
    margin: 10px;
}

/* mobile */

@media (max-width: 768px) {
    .carousel-inner>.carousel-item {
        height: 40vh;
    }
}
